import React, { Component } from 'react';
import './App.css';
import Movie from './Movie';

class App extends Component {
  // Render : componentWillMount -> render -> componentDidMount
  // Update : compotnenWillReceiveProps() -> shouldComponentUpdate() == true -> componentWillUpdate() -> render() -> componentDidUpdate()

  //component 내 state가 변경 될 때마다 다시 render()발생

  state = {
  }

  componentDidMount(){
    this._getMovies();
    

    // setTimeout(function(){
    //   // this.state.greeting = 'somethingelse';
    //   // this.state.greeting = 'somethingelse'; -> X : state를 직접 써서 update 하지 않는다.
    //   // state를 직접 변경하면 render의 lifecycle이 작동하지 않는다
    //   // state가 변경되면 다시 render()

    //   this.setState({
    //     greeting : 'Hello again~'
    //   })
    // },2000)
  }

  _getMovies = async () =>{
    const movies = await this._callApi();
    this.setState({
      // movies : movies 아래와 같은것
      movies
    })
  }

  _callApi = () => {
    return fetch('https://yts.am/api/v2/list_movies.json?sort_by=download_count')
    .then(response => response.json())
    .then(json => json.data.movies)
    .catch(err => console.log(err))
  }

  _renderMovies = () =>{
    const movies = this.state.movies.map((movie) => {
      return <Movie
        title={movie.title}
        poster={movie.medium_cover_image}
        key={movie.id}
        genres={movie.genres}
        synopsis={movie.synopsis}
        />
    })
    return movies
  }


  render() {
    const { movies } = this.state;
    return (
      <div className={movies ? "App" : "App--loading"}>
        {movies ? this._renderMovies() : 'Loading...'} 
      </div>
      
    );
  }
}

export default App;
